:root {
    --main-color: #1f2022;
    --main-bg: #fff;
    --boxed-width: 1140px;
    --navbar-color: #000;
    --navbar-bg: #fff;
    --navbar-2-color: #000;
    --navbar-2-bg: #fbfbfb;
    --navbar-dark-color: #fff;
    --navbar-dark-bg: #1f2022
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

html {
    font-size: 14px;
}

body {
    margin: 0;
    font-family: "Montserrat", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 2px rgba(0, 0, 0, .1)
}

::-webkit-scrollbar {
    background: hsla(0, 0%, 100%, .2);
    position: absolute;
    width: 2px;
}

::-webkit-scrollbar:hover {
    display: block;
}

::-webkit-scrollbar-thumb {
    background-color: #bcbcbd
}

::-webkit-scrollbar:horizontal {
    height: 2px
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

img {
    max-width: 100%;
}

.bg-image {
    height: 100%;
    background-position: center;
    background-size: cover;
}

.flex-1 {
    flex: 1;
}

.fs-12 {
    font-size: .75rem;
}

.mt-2 {
    margin-top: 1rem;
}

.float-right {
    float: right;
}

.ml-auto {
    margin-left: auto;
}

.main-page {
    padding: 3rem;
}

.main-page p {
    font-weight: 600;
}

.button {
    display: inline-block;
    text-decoration: none;
    font-weight: 600;
    cursor: pointer;
    background-color: #13c98a;
    line-height: 1;
    font-size: 1rem;
    border: 0;
    border-radius: 3rem;
    color: #fff;
    padding: 1rem 3rem;
}

.button:hover {
    background-color: #2e09b6;
}

.form-data .form-value {
    color: #aaa;
}

a,
a:hover {
    text-decoration: none;
    color: inherit;
}

.text-faded {
    color: rgba(0, 0, 0, .54) !important;
    fill: rgba(0, 0, 0, .54) !important;
}

.text-small {
    font-size: 0.8rem;
}

.strong{
  font-weight: bolder;
}

/***  PATIENT MODULE ****/
.avatar-box .custom-file{
    max-width: 250px;
}